import { createSlice } from '@reduxjs/toolkit';
import { API_TICKET } from '../../config';
import axios from '../../utils/axios';
import { dispatch } from '../store';

// ----------------------------------------------------------------------

const initialState = {
    isLoading: false,
    error: null,
    saved: null
};

const slice = createSlice({
    name: 'ticket',
    initialState,
    reducers: {
        // START LOADING
        startLoading(state) {
            state.isLoading = true;
        },

        // HAS ERROR
        hasError(state, action) {
            state.isLoading = false;
            state.error = action.payload;
        },

        setSavedTicket(state, action) {
            state.isLoading = false;
            state.saved = action.payload;
        }
    }
});

// Reducer
export default slice.reducer;

// Actions
export const { setSavedTicket } = slice.actions;

export function loadSavedTicket() {
    return async () => {
        dispatch(slice.actions.startLoading());
        try {
            axios
                .get(API_TICKET.getSavedTicket)
                .then((res) => {
                    if (res.data)
                        dispatch(slice.actions.setSavedTicket(res.data.data));
                    else
                        dispatch(slice.actions.setSavedTicket(null));

                })
                .catch((err) => {
                    dispatch(slice.actions.hasError(err));
                });
        } catch (err) { }
    };
}

export function loadSavedEditTicket(id) {
    return async () => {
        dispatch(slice.actions.startLoading());
        try {
            axios
                .get(API_TICKET.getSavedEditTicket, { params: { id } })
                .then((res) => {
                    if (res.data)
                        dispatch(slice.actions.setSavedTicket(res.data.data));
                    else
                        dispatch(slice.actions.setSavedTicket(null));
                })
                .catch((err) => {
                    dispatch(slice.actions.hasError(err));
                });
        } catch (err) { }
    };
}