import { createSlice } from '@reduxjs/toolkit';
import { API_NOTIFICATION } from '../../config';
import axios from '../../utils/axios';
import { dispatch } from '../store';

const initialState = {
    isLoading: false,
    error: null,
    notifications: []
};

const slice = createSlice({
    name: 'notification',
    initialState,
    reducers: {
        startLoading(state) {
            state.isLoading = true;
        },
        hasError(state, action) {
            state.isLoading = false;
            state.error = action.payload;
        },
        setNotifications(state, action) {
            state.isLoading = false;
            state.notifications = action.payload;
        }
    }
});

export default slice.reducer;

export const { setNotifications } = slice.actions;

export function loadUserNotifications() {
    return async () => {
        dispatch(slice.actions.startLoading());
        try {
            axios
                .get(API_NOTIFICATION.getUserNotifications)
                .then((res) => {
                    dispatch(slice.actions.setNotifications(res.data.data));
                })
                .catch((err) => {
                    dispatch(slice.actions.hasError(err));
                });
        } catch (err) {}
    };
}
