import { createContext, useEffect, useRef, useState } from 'react';
import { WEBSOCKET_CONSTATNT, WSS_HOST } from '../config';
import useAuth from '../hooks/useAuth';

export const WebsocketContext = createContext(false, null, () => { });

export const WebsocketProvider = ({ children }) => {
    const { user } = useAuth();
    const [isReady, setIsReady] = useState(false);
    const [received, setReceived] = useState(null);

    const ws = useRef(null);

    useEffect(() => {
        let url = WSS_HOST;
        if (user) {
            url = `${WSS_HOST}?token=${window.localStorage.getItem('accessToken')}`;
        }
        const socket = new WebSocket(url);

        socket.onopen = () => {
            setIsReady(true);
            if (user) {
                socket.send(
                    JSON.stringify({
                        command: WEBSOCKET_CONSTATNT.USER_LOGIN,
                        content: user?._id
                    })
                );
            }
        };
        socket.onclose = () => setIsReady(false);
        socket.onmessage = (event) => {
            try {
                setReceived(JSON.parse(event.data));
            } catch (err) {
                setReceived(event.data);
            }
        };

        ws.current = socket;

        return () => {
            socket.close();
        };
    }, [user]);

    const send = (data) => {
        if (isReady) {
            ws.current.send(JSON.stringify(data));
        } else {
            return false;
        }
    };

    return (
        <WebsocketContext.Provider
            value={{
                isReady,
                received,
                socket: ws.current,
                send
            }}
        >
            {children}
        </WebsocketContext.Provider>
    );
};
