import styled from 'styled-components';

const Input = styled.input`
    font-size: 18px;
    padding: 11px;
    display: inline-flex;
    justify-content: space-between;
    -webkit-box-align: center;
    width: 100%;
    border-bottom: 1px solid #757575;
    border-radius: 0.75rem;
    background: linear-gradient(rgb(28, 28, 28), rgb(28, 28, 28)) padding-box padding-box,
        linear-gradient(rgb(130, 130, 130), rgb(50, 50, 50)) border-box border-box;
    border-width: 1px;
    border-color rgba(0, 0, 0, 0);
    &:focus {
        outline: none;
    }
`;

const Wrapper = styled.div`
    position: relative;
`;

const Label = styled.label`
    color: #fff;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 20px;
    top: 14px;
    transition: 0.2s ease all;
    input:focus ~ &,
    input:not(:placeholder-shown).input:not(:focus) ~ .label,
    input:valid ~ & {
        top: -20px;
        font-size: 14px;
        left: 14px;
        border-radius: 8px;
    }
`;

export const FloatingInput = ({ label, labelStyle = {}, required = true, register, ...other }) => (
    <Wrapper>
        <Input {...other} {...register} required={required} />
        <Label style={labelStyle}>{label}</Label>
    </Wrapper>
);
