import Cookies from 'js-cookie';
import React, { useEffect } from 'react';
import { toast, ToastContainer } from 'react-toastify';

import GlobalStyle from './GlobalStyles';
import useLocales from './hooks/useLocales';

import { dispatch, useSelector } from './redux/store';
import { loadAvailableEvents } from './redux/slices/event';
import { loadUserNotifications, setNotifications } from './redux/slices/notification';

import Router from './routes';
import { WEBSOCKET_CONSTATNT } from './config';
import useAuth from './hooks/useAuth';
import useWebsocket from './hooks/useWebsocket';

export default function App() {
    const { onChangeLang } = useLocales();
    const { user } = useAuth();
    const { received } = useWebsocket();
    const { notifications } = useSelector((state) => state.notification);

    useEffect(() => {
        const lang = Cookies.get('i18nextLng') || 'pt';
        onChangeLang(lang);
        dispatch(loadAvailableEvents());
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        if (user != null && user) {
            dispatch(loadUserNotifications());
        }
    }, [user]);

    useEffect(() => {
        if (received && received?.command === WEBSOCKET_CONSTATNT.NOTIFICATION) {
            const { notify } = received;
            if (user?._id !== notify.sender) {
                const newNotifications = notifications?.reduce((s, item) => [...s, item], [received?.notify]);
                dispatch(setNotifications(newNotifications));
            }
            toast.success(received?.notify.content);
        }
    }, [received]);

    return (
        <>
            <GlobalStyle />
            <ToastContainer hideProgressBar />
            <Router />
        </>
    );
}
