import { Suspense, lazy } from 'react';
import { useRoutes } from 'react-router-dom';
import MainLayout from '../layouts/main';
import AuthGuard from '../utils/auth';
import LoadingScreen from '../components/LoadingScreen';

const Loadable = (Component) => (props) => {
    return (
        <Suspense fallback={<LoadingScreen />}>
            <Component {...props} />
        </Suspense>
    );
};

export default function Router() {
    return useRoutes([
        {
            path: '/',
            element: <MainLayout />,
            children: [{ element: <MainPage />, index: true }]
        },
        {
            path: '/profile',
            element: (
                <AuthGuard>
                    <MainLayout />
                </AuthGuard>
            ),
            children: [
                { element: <BasicProfilePage />, path: '' },
                { element: <ChangeEmailPage />, path: 'changemail' },
                { element: <ChangeTelephonePage />, path: 'changtelephone' },
                { element: <PaymentPage />, path: 'payments' },
                { element: <IdentifyPage />, path: 'identify' },
                { element: <NotificationPage />, path: 'notifications' }
            ]
        },
        {
            path: '/event',
            element: <MainLayout />,
            children: [
                { element: <AddEventPage />, path: 'add' },
                { element: <SearchEventPage />, path: 'search' },
                { element: <EventInformatioPage />, path: ':eventId/:tabId' },
                {
                    element: <EventInformatioPage />,
                    path: ':eventId/:tabId/:eventTypeId'
                },
                { element: <SearchEventsPage />, path: 'search_event' }
            ]
        },
        {
            path: '/cart',
            element: (
                <AuthGuard>
                    <MainLayout />
                </AuthGuard>
            ),
            children: [
                { element: <CartPage />, path: '' },
                { element: <CartPaymentPage />, path: 'payment' }
            ]
        },
        {
            path: '/ticket',
            element: <MainLayout />,
            children: [
                { element: <BuyTicketPage />, path: 'buy/:id' },
                {
                    element: (
                        <AuthGuard>
                            <AddTicketPage />
                        </AuthGuard>
                    ), path: 'sell/add'
                },
                {
                    element: (
                        <AuthGuard>
                            <TicketDetailPage />
                        </AuthGuard>
                    ), path: 'see/:id'
                },
                {
                    element: (
                        <AuthGuard>
                            <EditTicketPage />
                        </AuthGuard>
                    ), path: 'sell/edit/:tid'
                },
                {
                    element: (
                        <AuthGuard>
                            <ManageAdPage />
                        </AuthGuard>
                    ), path: 'manage/:id'
                },
                {
                    element: (
                        <AuthGuard>
                            <ADTicketPage />
                        </AuthGuard>
                    ), path: 'ad/:id'
                },
                {
                    element: (
                        <AuthGuard>
                            <AlertTicketListPage />
                        </AuthGuard>
                    ), path: 'alert'
                },
                {
                    element: (
                        <AuthGuard>
                            <MyTicketsPage />
                        </AuthGuard>
                    ), path: 'my-tickets'
                },
                {
                    element: (
                        <AuthGuard>
                            <TicketsListPage />
                        </AuthGuard>
                    ), path: 'list/:eventId'
                },
                {
                    element: (
                        <AuthGuard>
                            <MyListsPage />
                        </AuthGuard>
                    ), path: 'my-lists'
                },
                {
                    element: (
                        <AuthGuard>
                            <SavedTicketListPage />
                        </AuthGuard>
                    ), path: 'saved'
                },
                {
                    element: (
                        <AuthGuard>
                            <BillingAddressPage />
                        </AuthGuard>
                    ), path: 'billingaddress'
                }
            ]
        },
        {
            path: '/conversation',
            element: (
                <AuthGuard>
                    <MainLayout />
                </AuthGuard>
            ),
            children: [
                { element: <ConversationTicketsPage />, path: 'list' },
                { element: <ConversationYardPage />, path: 'yard/:ticketId/:isReceived' }
            ]
        }
    ]);
}

const MainPage = Loadable(lazy(() => import('../pages/Main')));

const CartPage = Loadable(lazy(() => import('../pages/cart')));
const CartPaymentPage = Loadable(lazy(() => import('../pages/cart/Payment')));

const ChangeEmailPage = Loadable(lazy(() => import('../pages/profile/ChangeEmail')));
const ChangeTelephonePage = Loadable(lazy(() => import('../pages/profile/ChangeTelephone')));
const PaymentPage = Loadable(lazy(() => import('../pages/profile/Payment')));
const IdentifyPage = Loadable(lazy(() => import('../pages/profile/Identify')));
const BasicProfilePage = Loadable(lazy(() => import('../pages/profile/BasicProfile')));
const NotificationPage = Loadable(lazy(() => import('../pages/profile/Notifications')));

const AddEventPage = Loadable(lazy(() => import('../pages/events/AddEvent')));
const SearchEventPage = Loadable(lazy(() => import('../pages/events/SearchEvent')));
const EventInformatioPage = Loadable(lazy(() => import('../pages/events/EventInformation')));
const SearchEventsPage = Loadable(lazy(() => import('../pages/events/SearchEvents')));

const MyListsPage = Loadable(lazy(() => import('../pages/ticket/MyLists')));
const ADTicketPage = Loadable(lazy(() => import('../pages/ticket/ADTicket')));
const AddTicketPage = Loadable(lazy(() => import('../pages/ticket/AddTicket')));
const TicketDetailPage = Loadable(lazy(() => import('../pages/ticket/TicketDetail')));
const EditTicketPage = Loadable(lazy(() => import('../pages/ticket/EditTicket')));
const BuyTicketPage = Loadable(lazy(() => import('../pages/ticket/BuyTicket')));
const MyTicketsPage = Loadable(lazy(() => import('../pages/ticket/MyTickets')));
const TicketsListPage = Loadable(lazy(() => import('../pages/ticket/TicketsList')));
const ManageAdPage = Loadable(lazy(() => import('../pages/ticket/ManageAd')));
const SavedTicketListPage = Loadable(lazy(() => import('../pages/ticket/SavedTicketList')));
const AlertTicketListPage = Loadable(lazy(() => import('../pages/ticket/AlertedTicketList')));
const BillingAddressPage = Loadable(lazy(() => import('../pages/ticket/BillingAddress')));

const ConversationYardPage = Loadable(lazy(() => import('../pages/conversation/ConversationYard')));
const ConversationTicketsPage = Loadable(lazy(() => import('../pages/conversation/ConversationTickets')));
