import { useMemo, useState } from 'react';
import { useForm, FormProvider } from 'react-hook-form';
import { toast } from 'react-toastify';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import { FloatingInput } from '../../components/form/FloatingInput';
import PhoneInput from 'react-phone-input-2'

export default function RegisterForm({ setFormData }) {
    const { t } = useLocales();
    const { signup } = useAuth();
    const [loading, setLoading] = useState(false);
    const { register, handleSubmit, getValues, setValue } = useForm({
        defaultValues: useMemo(() => {
            return {
                email: '',
                fullName: '',
                phone: '',
                password: ''
            };
        }, [])
    });

    const onSubmit = async (data, e) => {
        e.preventDefault();
        setLoading(true);
        const result = await signup(data);
        setLoading(false);
        if (result !== null && result.status === 201) {
            toast(result.data.message);
        } else if (result !== null && (result.status === 200 || result.status === 500)) {
            if (setFormData) setFormData(data);
        }
        return true;
    };

    const handleLogin = () => {
        document.querySelector('#register-modal-check').click();
        document.querySelector('#auth-modal-check').click();
    };

    return (
        <div className="w-full p-2">
            <div className="w-full flex flex-col" />
            <button
                type="button"
                className="btn flex items-center justify-center text-sm text-center text-black bg-white hover:bg-gray-100 rounded-lg capitalize gap-2 w-full"
            >
                <svg width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                        d="M24.2665 9.64975H23.2999V9.59995H12.4999V14.3999H19.2817C18.2923 17.1941 15.6337 19.1999 12.4999 19.1999C8.52376 19.1999 5.29998 15.9761 5.29998 11.9999C5.29998 8.02376 8.52376 4.79998 12.4999 4.79998C14.3353 4.79998 16.0051 5.49237 17.2765 6.62337L20.6707 3.22918C18.5275 1.23179 15.6607 0 12.4999 0C5.87297 0 0.5 5.37297 0.5 11.9999C0.5 18.6269 5.87297 23.9999 12.4999 23.9999C19.1269 23.9999 24.4999 18.6269 24.4999 11.9999C24.4999 11.1953 24.4171 10.4099 24.2665 9.64975Z"
                        fill="#FFC107"
                    />
                    <path
                        d="M1.88379 6.41457L5.82637 9.30595C6.89316 6.66477 9.47675 4.79998 12.5001 4.79998C14.3355 4.79998 16.0053 5.49237 17.2767 6.62337L20.6709 3.22918C18.5277 1.23179 15.6609 0 12.5001 0C7.89096 0 3.89378 2.60219 1.88379 6.41457Z"
                        fill="#FF3D00"
                    />
                    <path
                        d="M12.4994 24.0002C15.5989 24.0002 18.4153 22.814 20.5447 20.885L16.8307 17.7422C15.5855 18.6892 14.0638 19.2014 12.4994 19.2002C9.37817 19.2002 6.72798 17.21 5.72959 14.4326L1.81641 17.4476C3.8024 21.3338 7.83558 24.0002 12.4994 24.0002Z"
                        fill="#4CAF50"
                    />
                    <path
                        d="M24.2665 9.64941H23.2999V9.59961H12.5V14.3996H19.2818C18.8085 15.7294 17.956 16.8915 16.8296 17.7422L16.8314 17.741L20.5454 20.8838C20.2826 21.1226 24.4999 17.9996 24.4999 11.9996C24.4999 11.195 24.4171 10.4096 24.2665 9.64941Z"
                        fill="#1976D2"
                    />
                </svg>
                Continuar com Google
            </button>
            <button
                type="button"
                className="btn flex items-center justify-center text-sm text-center text-white bg-[#1877F2] hover:bg-[#1877F2] rounded-lg capitalize gap-2 w-full mt-3"
            >
                <svg width="26" height="24" viewBox="0 0 26 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                        fillRule="evenodd"
                        clipRule="evenodd"
                        d="M13.0001 0C19.668 0 25.0734 5.40546 25.0734 12.0733C25.0734 18.0995 20.6584 23.0943 14.8866 24V15.5633H17.6994L17.6997 15.5633L18.235 12.0733H14.8866V9.80864C14.8866 8.89111 15.3184 7.99587 16.683 7.92733C16.7318 7.92489 16.7817 7.92349 16.8328 7.92319C16.8398 7.92315 16.8468 7.92314 16.8538 7.92314H18.3762L18.3765 7.92313V4.95195C18.3765 4.95195 16.9949 4.71615 15.6739 4.71615C15.6523 4.71615 15.6308 4.71625 15.6094 4.71645C15.509 4.71741 15.4098 4.7206 15.312 4.72602C12.7594 4.86723 11.1133 6.52189 11.1133 9.41344V12.0733H8.04814V12.0733H8.04785V15.5633H11.1133V24C5.34168 23.0941 0.926758 18.0993 0.926758 12.0733C0.926758 5.40536 6.33222 0 13.0001 0Z"
                        fill="white"
                    />
                </svg>
                Continuar com Google
            </button>
            <div className="divider text-[#99A1A3] font-medium mb-8">OR</div>
            <FormProvider>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="w-full grid grid-cols-2 gap-4 mb-4 register-form">
                        <FloatingInput
                            labelStyle={{ color: '#828282' }}
                            register={register('fullName')}
                            type="text"
                            label={t('label.fullName')}
                        />
                        <FloatingInput
                            labelStyle={{ color: '#828282' }}
                            register={register('email')}
                            type="email"
                            label={t('label.email')}
                        />
                        <PhoneInput
                            country={'br'}
                            autoFormat
                            inputClass="ksdEjn input w-full"
                            value={getValues('phone')}
                            onChange={(value, country, e, formattedValue) => setValue('phone', formattedValue)}
                        />
                        <FloatingInput
                            labelStyle={{ color: '#828282' }}
                            register={register('password')}
                            label={t('label.password')}
                            type="password"
                        />
                    </div>

                    <div className="w-full flex flex-col gap-4 text-center">
                        <button
                            type="submit"
                            className={`${loading && 'loading'} btn btn-primary text-lg rounded-xl capitalize`}
                        >
                            Continuar com e-mail
                        </button>
                        <label className="text-[#99A1A3]">
                            Ao clicar em continuar, voc confirma que tem mais de 16 anos e concorda com nosso&nbsp;
                            <span onClick={handleLogin} className="cursor-pointer text-yellow-500 underline">
                                Termos e condies
                            </span>
                        </label>
                    </div>
                </form>
            </FormProvider>
        </div>
    );
}
