import { Link } from 'react-router-dom';
import { Icon } from '@iconify/react';
import styled from 'styled-components';

import useLocales from '../../hooks/useLocales';

import Logo from '../../components/Logo';
import MenuItem from '../../components/MenuItem';

import { useMediaQuery } from '@mui/material';

const RootStyle = styled.div`
    background: 100% 20% / 30% no-repeat url(/images/background/footer-bg.png),
        linear-gradient(253.21deg, #393939 -3.03%, #1c1c1c 40.15%);
    bottom: 0;
    height: 100%;
    z-index: -1;
    width: 100vw;
`;

export default function MainFooter() {
    const { t } = useLocales();
    const isMobile = useMediaQuery('(max-width:768px)');
    return (
        <RootStyle>
            <div className="w-full relative container py-5 px-2">
                <div className="w-full flex flex-col items-center gap-16 p-2 mb-20 mt-10">
                    <Logo direction="vertical" />
                    <div className="container">
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 md:gap-6 mb-8">
                            <div className={`flex flex-col gap-4 ${isMobile && 'items-center'}`}>
                                <label className="text-2xl">{t('title.about')}</label>
                                <p className="text-stone-400 text-sm">{t('description.about')}</p>
                                <label className="mb-8 text-stone-400 text-sm">
                                    {t('title.customer_service')} <br />
                                    contato@fanticket.com.br
                                </label>
                            </div>
                            <div className={`flex flex-col gap-2 ${isMobile && 'items-center'}`}>
                                <label className="text-2xl">{t('title.quick_access')}</label>
                                <MenuItem
                                    color="rgb(120 113 108)"
                                    className="text-[19px] text-stone-400 font-medium cursor-pointer"
                                >
                                    {t('title.start')}
                                </MenuItem>
                                <MenuItem
                                    color="rgb(120 113 108)"
                                    className="text-[19px] text-stone-400 font-medium cursor-pointer"
                                >
                                    {t('title.works')}
                                </MenuItem>
                                <MenuItem
                                    color="rgb(120 113 108)"
                                    className="text-[19px] text-stone-400 font-medium cursor-pointer"
                                >
                                    <label className="cursor-pointer" htmlFor="auth-modal-check">
                                        Login
                                    </label>
                                </MenuItem>
                                <MenuItem
                                    color="rgb(120 113 108)"
                                    className="text-[19px] text-stone-400 font-medium cursor-pointer"
                                >
                                    {t('title.terms')}
                                </MenuItem>
                                <MenuItem
                                    color="rgb(120 113 108)"
                                    className="text-[19px] text-stone-400 font-medium cursor-pointer"
                                >
                                    {t('title.privacy_policy')}
                                </MenuItem>
                            </div>
                            <div className={`flex flex-col gap-4 z-10 ${isMobile && 'items-center'}`}>
                                <label className="text-2xl">{t('title.social_media')}</label>
                                <div className="flex gap-2 mb-8">
                                    <Link className="btn btn-circle btn-outline btn-primary">
                                        <Icon icon="uit:facebook-f" width={24} />
                                    </Link>
                                    <Link className="btn btn-circle btn-outline btn-primary">
                                        <Icon icon="ph:instagram-logo-bold" width={24} />
                                    </Link>
                                    <Link className="btn btn-circle btn-outline btn-primary">
                                        <Icon icon="ri:youtube-line" width={24} />
                                    </Link>
                                    <Link className="btn btn-circle btn-outline btn-primary">
                                        <Icon icon="ph:twitter-logo-bold" width={24} />
                                    </Link>
                                </div>
                                <label className="text-2xl">{t('title.payment_methods')}</label>
                            </div>
                        </div>
                        <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
                            <div className={`md:col-span-3 ${isMobile && 'align-center'}`}>
                                <label className="text-stone-400 text-sm">{t('description.reserved')}</label>
                            </div>
                            <div className={`md:col-span-2 flex gap-4 flex-col md:flex-row ${isMobile && 'items-center'}`}>
                                <div className="flex gap-2 items-center">
                                    <label className="text-stone-400 text-sm">{t('title.designed_by')}:</label>
                                    <div>
                                        <img
                                            src="/images/footer/designer-logo.png"
                                            alt="desinger"
                                            style={{ width: '100%', height: '100%' }}
                                        />
                                    </div>
                                </div>
                                <div className="flex gap-2 items-center">
                                    <label className="text-stone-400 text-sm">{t('title.developed_by')}:</label>
                                    <div>
                                        <img
                                            src="/images/footer/developer-logo.png"
                                            alt="developr"
                                            style={{ width: '100%', height: '100%' }}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </RootStyle>
    );
}
