import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Icon } from '@iconify/react';

import { Drawer } from '@mui/material';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';
import useOffSetTop from '../../hooks/useOffSetTop';

import Logo from '../../components/Logo';
import MenuItem from '../../components/MenuItem';

import UserPopover from './UserPopover';

export default function MainHeader() {
    const { t } = useLocales();
    const { user, isAuthenticated, logout } = useAuth();
    const isScrolled = useOffSetTop(10);
    const navigate = useNavigate();

    const [state, setState] = useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });

    const _onSellTicket = () => {
        if (isAuthenticated) navigate('/ticket/sell/add');
        else {
            document.querySelector('#auth-modal-check').click();
        }
    };

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setState({ ...state, [anchor]: open });
    };

    const Header = () => (
        <>
            {isAuthenticated ? (
                <>
                    <MenuItem className="text-sm/[17px] cursor-pointer">
                        {t('title.works')}
                    </MenuItem>
                    <MenuItem className="text-sm/[17px] cursor-pointer" onClick={() => navigate('/cart')}>
                        {t('title.menu.cart')}
                    </MenuItem>
                    <MenuItem className="text-sm/[17px] cursor-pointer w-10">{t('action.help')}</MenuItem>
                    <UserPopover user={user} logout={logout} />
                </>
            ) : (
                <>
                    <MenuItem className="text-sm/[17px] cursor-pointer">{t('title.works')}</MenuItem>
                    <MenuItem className="text-sm/[17px] text-center">
                        <label className="cursor-pointer" htmlFor="auth-modal-check">
                            Login
                        </label>
                    </MenuItem>
                </>
            )}
            <button
                className="btn btn-outline btn-primary py-2 px-6 text-sm/[17px] capitalize border-2 rounded-xl h-auto min-h-0"
                onClick={_onSellTicket}
            >
                {t('title.sell_ticket')}
            </button>
        </>
    )

    const list = (anchor) => (
        <div
            style={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250, color: "white" }}
            role="presentation"
            // onClick={toggleDrawer(anchor, false)}
            onKeyDown={toggleDrawer(anchor, false)}
        >
            <div className="w-full flex gap-5 items-center bg-black p-4 justify-end">
                <Header />
            </div>
        </div>
    );

    return (
        <div className={`w-full fixed top-0 left-0 z-50 ${isScrolled ? 'bg-base-100/50 backdrop-blur-md' : ''}`}>
            <div
                className={`transition-all duration-300 container flex justify-between p-2 ${isScrolled ? 'py-5' : 'py-8'
                    } items-center text-xl`}
            >
                <Logo />
                <button onClick={toggleDrawer('top', true)} className="btn btn-sm btn-circle btn-outline md:hidden btn-primary">
                    <Icon icon="ic:round-menu" />
                </button>
                <Drawer
                    anchor={'top'}
                    open={state['top']}
                    onClose={toggleDrawer('top', false)}
                >
                    {list('top')}
                </Drawer>
                <div className="md:flex gap-5 items-center hidden">
                    <Header />
                </div>
            </div>
        </div>
    );
}
