import { createContext, useState } from 'react';
import { useTranslation } from 'react-i18next';
import cookies from 'js-cookie';
import { setAcceptLanguage } from '../utils/jwt';

const LANGS = [
    {
        label: 'English',
        value: 'en',
        icon: '/icons/flag_england.svg',
        lang: 'en-US'
    },
    {
        label: 'Portugus',
        value: 'pt',
        icon: '/icons/flag_brazil.svg',
        lang: 'pt-BR'
    }
];

export const LocalesContext = createContext(false, null, () => {});

export const LocalesProvider = ({ children }) => {
    const { i18n, t } = useTranslation();
    const [currLng, setCurrLng] = useState('pt');
    const onChangeLang = (newlang) => {
        setCurrLng(newlang);
        i18n.changeLanguage(newlang);
        cookies.set('i18nextLng', newlang);
        setAcceptLanguage(newlang);
    };

    return (
        <LocalesContext.Provider
            value={{
                onChangeLang,
                t,
                currentLanguage: currLng,
                supportLangs: LANGS
            }}
        >
            {children}
        </LocalesContext.Provider>
    );
};
