import { useState } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Icon } from '@iconify/react';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import OtpInputCompoent from '../../components/form/OtpInputComponent';

export default function OtpVerificationSection({ email, password, fullName }) {
    const { t } = useLocales();
    const [otp, setOtp] = useState('');
    const { verifyEmailOtp, signup } = useAuth();
    const [sent, setSent] = useState(false);
    const onChangeOtp = (value) => {
        setOtp(value);
    };

    const onSubmit = async () => {
        const res = await verifyEmailOtp(email, otp);

        if (res.status === 200) {
            toast.success(res.message);
            window.location.reload();
        } else {
            toast.error(res.data.message);
        }
    };

    const handleResent = async () => {
        setSent(true);
        await signup(email, fullName, password);
        setSent(false);
    };

    return (
        <>
            <div className="flex flex-col gap-4 p-2 mb-4">
                <label className="text-stone-400 mb-4">
                    {t('description.already_sent_otp', {
                        address: email
                    })}
                </label>
                {sent && (
                    <div className="flex justify-center w-full">
                        <Icon icon="line-md:loading-twotone-loop" width={48} />
                    </div>
                )}
                <div className="w-full justify-between gap-2 flex">
                    <label>{t('label.verification_code')}</label>
                    <Link href="#" className="text-primary" onClick={handleResent}>
                        {t('action.resend')}
                    </Link>
                </div>

                <OtpInputCompoent value={otp} onChange={onChangeOtp} />
                <p className="text-sm text-stone-500">Insira seu cdigo de 6 dgitos aqui</p>
                <div className="flex items-center mr-4">
                    <input type="checkbox" className="border-gray-300 rounded h-10 w-10" />
                    <label className="ml-2">
                        Eu gostaria de receber recomendaes e contedos sobre o evento atravs do e-mail.
                    </label>
                </div>
            </div>
            <button
                className="btn btn-primary px-8 capitalize w-full disabled:btn-secondary"
                disabled={otp.length !== 6}
                onClick={onSubmit}
            >
                {t('action.create_account')}
            </button>
        </>
    );
}
