import { useState } from 'react';

import { BlurModalBox } from '../../components/StyledComponents';

import OtpVerificationSection from '../../sections/auth/OtpVerification';
import RegisterForm from '../../sections/auth/RegisterForm';

export default function Register() {
    const [step, setStep] = useState(0);
    const [data, setData] = useState();
    const changeUI = (formData) => {
        setData(formData);
        setStep(1);
    };
    return (
        <>
            <input type="checkbox" id="register-modal-check" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="register-modal-check"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0 text-white"
                        >
                            
                        </label>
                        <h3 className="font-bold text-3xl/[28px]">Cadastro</h3>
                    </div>

                    <div className="-mx-6 divider" />
                    {step === 0 && <RegisterForm setFormData={changeUI} />}
                    {step === 1 && (
                        <OtpVerificationSection
                            email={data?.email}
                            fullName={data?.fullName}
                            password={data?.password}
                        />
                    )}
                </BlurModalBox>
            </div>
        </>
    );
}
