import { LoginForm } from '../../sections/auth';
import { BlurModalBox } from '../../components/StyledComponents';

export default function Login() {
    return (
        <>
            <input type="checkbox" id="auth-modal-check" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="auth-modal-check"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0 text-white"
                        >
                            
                        </label>
                        <h3 className="font-bold text-3xl/[28px]">Login</h3>
                    </div>

                    <div className="-mx-6 divider" />
                    <LoginForm />
                </BlurModalBox>
            </div>
        </>
    );
}
