import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import { useEffect, useMemo } from 'react';

export default function RouterProgressBar() {
    NProgress.configure({
        showSpinner: false
    });

    useMemo(() => {
        NProgress.start();
    }, []);

    useEffect(() => {
        NProgress.done();
    }, []);

    return null;
}
